/*Catalog Section-----------------------------------------------------*/
/*--------------------------------------------------------------------*/
/*--------------------------------------------------------------------*/
/*--------------------------------------------------------------------*/
.ca-container {
  height: 700px;
}
.catalog {
  h1,
  h3 {
    text-align: center;
  }
  h2 {
    position: relative;
    top: 50%;
    text-align: right;
    padding-right: 2rem;
    color: $tuatara;
  }
  h3 {
    color: $blue;
    padding: 3rem 0 0;
    font-weight: 700;
    font-size: 1.1rem;
    text-transform: uppercase;
    margin-bottom: 20px;
    height: 85px;
    text-align: center;
    text-shadow: 0 1px 1px #e4ebe9;
  }
  h4 {
    font-size: 1rem;
    font-weight: 700;
  }
}
.catalog-item {
  padding: 2rem;
}
.back-end-button {
  width: 14.9rem;
}
.ecommerce-button {
  width: 14rem;
}
@import "lightbox";
/* Circular Content Carousel Style */
.ca-container {
  height: 700px;
}
@include media-breakpoint-up(up) {
  .catalog {
    img {
      transform: scale(1);
      transition: all 0.5s ease-out;
      &:hover {
        transform-origin: bottom;
        transform: scale(1.15);
        transition: transform 10s ease-out;
      }
    }
    .ca-item {
      padding: 1.3rem;
      background-color: transparent;
      transition: all 0.5s ease-in;
      &:hover {
        opacity: 0.8;
        background-color: $pink;
        color: $white;
        transition: all 0.5s ease-in;
        h3,
        .home-button {
          color: $white;
          text-shadow: none;
          transition: all 0.5s ease-in;
        }
        h3 {
          color: darken(pink, 70%);
        }
      }
    }
    .ca-item-4,
    .ca-item-2 {
      border-right: 1px solid $iron;
    }
